import React, { useState, useContext } from 'react'
import { AlertContex } from '../context/alert/alertContext'
import { FirebaseContext } from '../context/firesbase/FiresbaseContext'

export const Form = () => {

	const [value, setValue] = useState('')
	const alert = useContext(AlertContex)
	const firebase = useContext(FirebaseContext)

	const submitHandler = event => {
		event.preventDefault()

		if (value.trim()) {
			firebase.addNote(value.trim()).then(() => {
				alert.show('Заметка была создана', 'success')
			}).catch(() => {
				alert.show('Заметка не была создана', 'danger')
			})
			setValue('')
		} else {
			alert.show('Введите название заметки')
		}

	}

	return (
		<form onSubmit={submitHandler}>
			<div className="form-group">
				<input className="form-control" type="text" placeholder="Введите на заметку..." value={value} onChange={e => setValue(e.target.value)} />
			</div>
		</form>
	)
}
